import { z } from 'zod';
import api from '@/lib/axios';
import { usePaginatedQuery } from '@/lib/react-query/hooks';
import { DisplayList, PaginatedDisplayListList } from '@/lib/generated/api-schemas';

type DisplayType = z.infer<typeof DisplayList>;

export function useDisplaysQuery() {
  return usePaginatedQuery<DisplayType>(
    ['displays'],
    async () => {
      const response = await api.get('/displays/');
      return PaginatedDisplayListList.parse(response.data);
    }
  );
}

